<template>
    <div class="contentBox accManage">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/busiCust/queryCusts" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="">
                            <el-input placeholder="" v-model="form.orgName"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input placeholder="" v-model="form.custName"></el-input>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query"></el-button>
                            <el-button type="primary" @click="add"></el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="loginId" label=""> </el-table-column>
                <el-table-column prop="orgName" label=""></el-table-column>
                <el-table-column prop="roleName" label=""> </el-table-column>
                <el-table-column prop="userName" label="" width="80"> </el-table-column>
                <!-- <el-table-column prop="userTel" label=""> </el-table-column> -->
                <el-table-column prop="userDept" label=""> </el-table-column>
                <el-table-column prop="instUserName" label=""> </el-table-column>
                <el-table-column prop="loginTime" label="">
                     <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.loginTime, "YYYY-MM-DD HH:mm:ss")}}</template>
                </el-table-column>
                <el-table-column prop="status" label="" width="80">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                </el-table-column>
                <el-table-column label="" width="150">
                    <template slot-scope="{row, index}">
                        <el-switch on-value="00001001" off-value="00001002" v-model="row.status" on-text="" off-text="" @change="changStatus(row)"></el-switch>
                        <el-tooltip class="item" effect="light" content=""  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" @click.stop="editAcct(row)" class="iconfont icon-bianji1"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" content=""  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-zhongzhimima" @click.stop="resetSysUserPwd(row)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
        <!-- dialog -->
        <self-dialog title="" v-model="addAcctFormVisible" @close="closeAddAcctForm">
            <template slot-scope="{close}">
                <add-acct-form :close="close"/>
            </template>
        </self-dialog>
        <!-- dialog -->
        <self-dialog ref="editAcctForm" title="" v-model="editAcctFormVisible" @close="closeEditAcctForm">
            <template slot-scope="{close, params}">
                <edit-acct-form :close="close" :params="params"/>
            </template>
        </self-dialog>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import addAcctForm from '@admin/page/module/addAcctForm'
import editAcctForm from '@admin/page/module/editAcctForm'
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//
require('@/store/cache').cacheCodes("status")
@Component
@InjectComponents({ BreadCrumb,addAcctForm,editAcctForm })
export default class accManage extends Vue {
    //
    @State(state => state.cache.codeStorage) codeStorage
    addAcctFormVisible = false
    editAcctFormVisible = false
    queryForm = {}
    // 
    add() {
        this.addAcctFormVisible =  true
    }
    closeAddAcctForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//
    }
    // 
    async changStatus(row){
        const newForm = {"userId":row.userId}
        const rsp = await this.$helper.fetch("api/busiCust/delCust").setParams(newForm).send()
        this.$refs.grid.load({"pageNo":1});//
        if (!rsp.success) return
        this.$notify({title: '',message:  rsp.message,type: 'success',duration:4000});
    }
    //
    editAcct(row){
          if(row.status =='00001002'){
          this.$notify({title: '',message:  '',type: 'error',duration:4000});
          return
    }
        this.editAcctFormVisible =  true
        this.$refs.editAcctForm.setParams(row)
    }
    closeEditAcctForm(){
        this.$refs.grid.load({"pageNo":1});//
    }
    //
    async resetSysUserPwd(row){
          if(row.status =='00001002'){
          this.$notify({title: '',message:  '',type: 'error',duration:4000});
          return
        }
        await this.$confirm(', ?', '')
        const rsp = await this.$helper.fetch("/api/busiCust/restPwd").setParam("userId",row.userId).send()
        if (!rsp.success) return
        await this.$alert('123456', '')
        this.$refs.grid.load({"pageNo":1});//
       // this.$notify({title: '',message:  rsp.message,type: 'success',duration:4000});
    }
}
</script>


