import Vue from 'vue'
import Router from 'vue-router'
import beforeEach from '@/router/beforeEach'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [
    { path: '/login', name: 'login', component: require('@admin/page/Login').default },
    { path: "/html", name: "index", component: require('@admin/components/Layout').default, children: [
      //{ path: "index", name: "index", component: require('@admin/page/SysUserManage').default, meta: ['', ''] },
      { path: 'sysUserManage', name: "sysUserManage", component: require('@admin/page/SysUserManage').default,meta: ['', ''],},
      { path: 'roleManage', name: "roleManage", component: require('@admin/page/roleManageWrapper').default,meta: ['', ''],},
      { path: 'codeManage', name: "codeManage", component: require('@admin/page/CodeManage').default,meta: ['', ''],},
      { path: 'menuManage', name: "menuManage", component: require('@admin/page/menuManage').default,meta: ['', ''],},
      { path: 'riskProduct', name: 'riskProduct', component: require('@admin/page/riskProductWrapper').default,meta: ['', ''],},
      { path: 'riskIndex', name: 'riskIndex', component: require('@admin/page/riskIndex').default,meta: ['', ''],},
      { path: 'orgRecharge', name: 'orgRecharge' ,component: require('@admin/page/orgRecharge').default,meta: ['', ''],},
      { path: 'consumeDetail', name: 'consumeDetail', component: require('@admin/page/consumeDetail').default,meta: ['', ''],},
      { path: 'orgManage', name: 'orgManage', component: require('@admin/page/orgManageWrapper').default,meta: ['', ''],},
      { path: 'accManage', name: 'accManage', component: require('@admin/page/accManage').default,meta: ['', ''],},
      { path: 'log', name: "log", component: require('@admin/page/log').default,meta: ['', ''],},
      { path: 'operatelog', name: "operatelog", component: require('@admin/page/operatelog').default,meta: ['', ''],},      
      { path: "unauthorized", name: "unauthorized", component: require('@admin/page/NotFound').default },
      { path: "*", name: "404", component: require('@admin/page/NotFound').default }
    ]},
    { path: "/index", redirect: { name: "index"} },
    { path: "/", redirect: { name: "index"} },
    { path: "*", redirect: {name: '404'} }
  ]
})

const perms = ['404', 'login', 'unauthorized', 'index', 'account']
const roles = []
router.beforeEach(async function (to, from, next) {
  await beforeEach({to, from, next, router, perms, roles})
})

export default router



