<template>
    <div class="roleManage contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/prod/queryList" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label="">
                            <el-input placeholder="" v-model="form.prodName"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="form.mngType" clearable placeholder="">
                                <el-option v-for="(item, index) in codeStorage.dataSource('mngType')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="form.status" clearable placeholder="">
                                <el-option v-for="(item, index) in codeStorage.dataSource('status')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query"></el-button>
                            <el-button type="primary" @click="editProduct()"></el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="prodId" label="ID"width="250"> </el-table-column>
                <el-table-column prop="prodName" label=""> </el-table-column>
                <el-table-column prop="costAmt" label="" width="60"> </el-table-column>
                <el-table-column prop="mngType" label="" width="80">
                    <template slot-scope="{row}">{{ codeStorage.get('mngType', row.mngType) }}</template>
                </el-table-column>
                <el-table-column prop="status" label="" width="60">
                    <template slot-scope="{row}">{{ codeStorage.get('status', row.status) }}</template>
                </el-table-column>
               <el-table-column prop="userName" label="" width="80"> </el-table-column>
                <el-table-column prop="insertTime" label="">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD HH:mm:ss")}}</template>
                </el-table-column>
                <el-table-column label="" width="150">
                    <template slot-scope="{row, index}">
                        <el-tooltip class="item" effect="light" content=""  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-guanlian" @click.stop="couplingIndex(row)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" content=""  placement="bottom-start" :visible-arrow="false">
                            <el-button  type="text" class="iconfont icon-bianji1" @click.stop="editProduct(row)"></el-button>
                        </el-tooltip>
                        <template v-if="row.status == '00001001'">
                            <el-tooltip class="item" effect="light" content=""  placement="bottom-start" :visible-arrow="false">
                                <el-button  type="text" class="iconfont icon-xiajia" @click.stop="changStatus(row)"></el-button>
                            </el-tooltip>
                        </template>
                        <template v-if="row.status == '00001002'">
                            <el-tooltip class="item" effect="light" content=""  placement="bottom-start" :visible-arrow="false">
                                <el-button  type="text" class="iconfont icon-shangjia" @click.stop="changStatus(row)"></el-button>
                            </el-tooltip>
                        </template>
                    </template>
                </el-table-column>
            </self-grid>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//
require('@/store/cache').cacheCodes('userType', "status","mngType")
@Component
@InjectComponents({ BreadCrumb })
export default class SysUserManage extends Vue {
    //
    @State(state => state.cache.codeStorage) codeStorage
    pars ={}
    queryForm = {}
    states = null
    // 
    async changStatus(row){
         if(row.status =='00001002') await this.$confirm(""+row.prodName+" ?",'')
         else{await this.$confirm(""+row.prodName+" ?",'')}
        const prodId = row.prodId
        const rsp = await this.$helper.fetch("/api/prod/alterStatus").setParam("prodId",prodId).send()
        this.$refs.grid.load({"pageNo":1});//
        if (!rsp.success) return
        this.$notify({title: '',message:  rsp.message,type: 'success',duration:4000});
    }
    // 
    async couplingIndex(row){
        if(row.status =='00001002'){
            this.$notify({title: '',message:  '',type: 'error',duration:4000});
            return
        }
        // if(!row) {row={}}
        row.emitFunction = 'couplingIndex'
        this.$emit('open',row);
    }
    // |
    async editProduct(row){
        if(!row) {row={}}else{
            if(row.status =='00001002'){
                this.$notify({title: '',message:  '',type: 'error',duration:4000});
                return
            }
        }
        this.$emit('open',row);
    }
}
</script>


