<template>
    <div class="contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
             <self-grid ref="grid" url="/api/log/getLogLists" v-model="queryForm" :multi="false" :checkBox="false">
                <template slot="query" slot-scope="{form, query, reset}">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item label=":">
                            <el-input v-model="form.orgName" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.prodName" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                           <el-date-picker @change="value => form.instDate = value" v-model="form.instDate_" type="date" placeholder=""></el-date-picker>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-select v-model="form.logState" clearable placeholder="">
                                <el-option v-for="(item, index) in codeStorage.dataSource('logState')" :key="index" :label="item.label" :value="item.value"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item class="buttonTeam">
                            <el-button type="warning" @click="query"></el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <el-table-column prop="orgName" label=""> </el-table-column>
                <el-table-column prop="userName" label=""> </el-table-column>
                <el-table-column prop="custName" label=""> </el-table-column>
                <el-table-column prop="insertTime" label="">
                    <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD")}}</template>
                </el-table-column>
                <el-table-column prop="prodName" label=""> </el-table-column>
                <el-table-column prop="ruleId" label=""> </el-table-column>
                <el-table-column prop="logState" label="">
                    <template slot-scope="{row}">{{ codeStorage.get('logState', row.logState) }}</template>
                </el-table-column>
                <el-table-column prop="errorReason" label=""> </el-table-column>
            </self-grid>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import BreadCrumb from "@/components/breadCrumb";
import { Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State } from "vue-decorators";
//
require('@/store/cache').cacheCodes("logState")
@Component
@InjectComponents({ BreadCrumb })
export default class log extends Vue {
    //
    @State(state => state.cache.codeStorage) codeStorage
    queryForm = {}
}
</script>


