<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label=":" prop="loginId">
        <el-input v-model="form.loginId" auto-complete="off"  placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="userName">
        <el-input v-model="form.userName" auto-complete="off"  placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="roleIds">
      <el-select v-model="form.roleIds" multiple clearable placeholder="">
          <el-option v-for="item in allRole" :key="item.roleId" :label="item.roleName" :value="item.roleId"></el-option>
      </el-select>
    </el-form-item>
    <el-row class="dialogButton">
      <el-col :span="12"><el-button type="info" @click="close" style="float:right"> </el-button></el-col>
      <el-col :span="12"><el-button type="primary" @click="submit" :loading="submitLoading"> </el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'
@Component
export default class ModifyLoginPwdForm extends Vue{
  @Getter('auth/userName') userName
  @Prop({type: Function, required: true, default: null}) close
  submitLoading = false
  form = {loginId: '', userName: '',roleIds: []}
  // 
  allRole = []
  async queryAllRole(){
      const rsp = await this.$helper.fetch("/api/user/queryAllRole").send()
      if (!rsp.success) return
      this.allRole = rsp.data.list
  }
  @Lifecycle created() {
      this.queryAllRole()
  }
  rules = {
    loginId: [
      {trigger: "blur", required: true, message: ''},
      {trigger: 'blur', max: 36, message: '36' },
      {whitespace: true, message: ''},
       {trigger: "blur,change", validator: (rule, value, callback) => {
          if (value.match("^[0-9a-zA-Z]+$")) return callback()
          return callback('')
      }}
    ],
    userName: [
      {trigger: "blur", required: true, message: ''},
      {trigger: 'blur', max: 32, message: '32' },
      {whitespace: true, message: ''},
      {trigger: "blur,change", validator: (rule, value, callback) => {
         var regex =/^[\u4e00-\u9fa5]+$/
          if (regex.test(value)) return callback()
          return callback(new Error(``))
      }}
    ],
    roleIds: [
      {type: 'array',trigger: 'change', required: true, message: ''}
    ]
  }
  async submit() {
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
    this.submitLoading =true
    const rsp = await this.$helper.fetch('/api/user/saveUser').setParams({...this.form}).send()
    this.submitLoading = false
    if (!rsp.success) return rsp.success
     this.$alert('123456', '')
    this.close()
  }
}
</script>




